.rent__pictures {
  position: relative;
  width: 100%;
  height: 415px;
  overflow: hidden;
  margin-bottom: 30px;
  border-radius: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.rent__pictures .pictures {
  position: absolute;
  width: 100%;
  object-fit: cover;
}
.rent__pictures .overlay {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.rent__pictures .overlay .arrows {
  position: absolute;
  top: 37%;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.rent__pictures .overlay .count {
  width: 24px;
  height: 32px;
  color: white;
  font-family: "Montserrat", sans-serif;
  font-size: 24px;
  position: relative;
  padding-bottom: 20px;
}