h1, h2, h3, Link {
  margin: 0;
}

.error {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  color: rgb(255, 97, 97);
  font-family: "Montserrat", sans-serif;
}
.error h1 {
  font-size: 15rem;
  font-weight: 700;
  margin: 139px 0;
}
.error h3 {
  font-size: 36px;
  margin-bottom: 139px;
}
.error__link {
  font-size: 18px;
  color: rgb(255, 97, 97);
  margin-bottom: 89px;
}