.banner {
  margin: 0;
  width: 100%;
  height: 223px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 25px;
  overflow: hidden;
  position: relative;
}
.banner img {
  width: 100%;
  border-radius: inherit;
  filter: brightness(0.7);
}
.banner__title {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.banner__title h1 {
  color: white;
  font-family: "Montserrat", sans-serif;
  font-size: 48px;
  font-weight: 400;
  letter-spacing: 2px;
}