.gallery {
  width: 100%;
  background-color: #F6F6F6;
  margin: 43px 0 0 0;
  border-radius: 25px;
  &__cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding: 25px 50px 50px 50px;
    margin: -15px;
    width: 100%;
  }
  &__card {
    width: calc(31% - 30px);
    padding-bottom: calc(31% - 30px);
    border-radius: 10px;
    position: relative;
    transition: all 0.3s ease;
    margin: 20px;
    margin-top: 25px;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      background: linear-gradient(180deg, rgba(255,255,255,0) 50%, rgba(0,0,0,0.4) 100%);
      border-radius: 10px;
      width: 100%;
      height: 100%;
    }
    &__title {
      width: 30%;
      position: absolute;
      bottom: 20px;
      left: 20px;
      color: white;
      font-family: "Montserrat", sans-serif;
      letter-spacing: 1px;
      z-index: 1;
    }
    &__img {
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: 50% 50%;
      border-radius: 10px;
    }    
  }
}
